<template>
    <b-container>
        <b-card>
            <b-card-img :src="'/image/'+recipe.imageName" img-top class="border-0 rounded-0 shadow-sm">
                <b-card-title>{{recipe.title}}</b-card-title>
                <b-card-body>
                    <b-list-group>
                        <b-list-group-item v-for="(ingredient) in recipe.ingredients" v-bind:key="ingredient">
                            {{ingredient}}
                        </b-list-group-item>
                    </b-list-group>
                    <b-card-text>
                        {{recipe.content}}
                    </b-card-text>
                    <b-button variant="link" class="text-danger-on-hover" v-on:click="removeRecipe()" v-if="this.isAdmin"><font-awesome-icon :icon="['fas','trash']"></font-awesome-icon></b-button>
                    <b-card-footer>
                        <b-list-group-item v-for="(comment) in comments" v-bind:key="comment.uuid">
                            <h6>{{comment.authorUsername}}</h6>
                            <p>{{comment.text}}</p>
                            <b-button variant="link" class="text-danger-on-hover" v-on:click="removeComment(comment.uuid)" v-if="this.isAdmin"><font-awesome-icon :icon="['fas','trash']"></font-awesome-icon></b-button>
                        </b-list-group-item>
                        <b-form v-if="this.isLoggedIn">
                            <b-form-group label="New comment: ">
                                <b-input id="commenttext"
                                         type="textarea"
                                         rows="3"
                                         no-resize
                                         v-model="this.text"
                                         :state="text.length>=0 && text.length <= 500"
                                >
                                </b-input>
                            </b-form-group>
                            <b-form-group>
                                <b-button type="primary" v-on:click="this.addComment()">Send</b-button>
                            </b-form-group>
                        </b-form>
                    </b-card-footer>
                </b-card-body>
            </b-card-img>
        </b-card>
    </b-container>
</template>

<script>
    export default {
        name: "Details",
        data()
        {
            return{ recipe: null, id: null, comments: [], isLoggedIn: false, isAdmin: false, text: ""};
        },
        beforeMount()
        {
            this.loadRecipe();
            this.loadComments();
            let user = JSON.parse(localStorage.getItem('recipe-site.user'));
            if(user)
            {
                this.isLoggedIn=true;
                if(user.roles.includes("ADMIN"))
                {
                    this.isAdmin = true;
                }
            }
            else
            {
                this.isLoggedIn=false;
            }
        },
        methods : {
            addComment()
            {
                window.axios.post('api/recipe/'+this.id+'/comment',{text:this.text}).then(res => {
                    this.recipe = res.data;
                }).catch(()=>console.log("error loading recipe data"));
            },
            removeComment(uuid)
            {
                window.axios.delete('api/recipe/'+this.id+'/comment/'+uuid).catch(()=>console.log("error deleting comment"));
            },
            removeRecipe()
            {
                window.axios.delete('api/recipe/'+this.id).then(() => {
                    this.$router.push({name:"index"});
                }).catch(()=>console.log("error deleting recipe"));
            },
            loadRecipe()
            {
                this.id = this.$route.params.id;
                window.axios.get('api/recipe/'+this.id).then(res => {
                    this.recipe = res.data;
                }).catch(()=>console.log("error loading recipe data"));
            },
            loadComments()
            {
                window.axios.get('api/recipe/'+this.id+'/comments').then(res => {
                    this.comments = res.data;
                }).catch(()=>console.log("error loading recipe comments"));
            }
        }
    }
</script>

<style scoped>
    .text-danger-on-hover:hover
    {
        color:#e3342f;
    }
    .text-danger-on-hover
    {
        color:#6c757d;
    }
</style>
