<template>
    <b-container>
        <b-list-group horizontal>
            <b-list-group-item>
                <b-form inline>
                    <b-form-input type="text" v-model="this.filterType"></b-form-input>
                </b-form>
            </b-list-group-item>
            <b-list-group-item>
                <b-form inline>
                    <b-form-input type="text" v-model="this.filterAuthorName"></b-form-input>
                </b-form>
            </b-list-group-item>
            <b-list-group-item>
                <b-form inline>
                    <b-form-input type="number" v-model="this.filterRating"></b-form-input>
                </b-form>
            </b-list-group-item>
            <b-list-group-item>
                <b-form inline>
                    <b-button variant="primary" v-on:click="this.resetFilters()">Reset</b-button>
                    <b-button variant="primary" v-on:click="this.filterRecipes()">Filter</b-button>
                </b-form>
            </b-list-group-item>
        </b-list-group>
        <b-card-group deck>
            <b-card v-if="this.isLoggedIn" class="shadow-sm mb-5 rounded-0 border-0 col-md-3"
                    img-top
                    style="min-width: 18rem;max-width: 319px;"
            >
                <b-card-body>
                    <b-form-rating show-value
                                   show-value-max
                                   precision="1"
                                   no-border
                                   readonly>
                    </b-form-rating>
                    <b-button variant="link" class="text-decoration-none" v-on:click="this.showModal()">
                        <p>
                            Add new recipe
                        </p>
                    </b-button>
                </b-card-body>
            </b-card>
            <b-card v-for="(recipe) in recipes" :key="recipe.uuid" class="shadow-sm mb-5 rounded-0 border-0 col-md-3"
                img-top
                v-bind:img-src="recipe.imageName"
                style="min-width: 18rem;max-width: 319px;"
            >
                <b-card-body>
                    <b-form-rating :v-model="recipe.difficultyRating"
                                   show-value
                                   show-value-max
                                   precision="1"
                                   no-border
                                   readonly>
                    </b-form-rating>
                    <b-button variant="link" class="text-decoration-none" :to="{name: 'recipe-details', params: { id: recipe.uuid }}">
                        <p>
                            {{recipe.title}}
                        </p>
                    </b-button>
                </b-card-body>
            </b-card>
        </b-card-group>
        <b-modal id="addNewRecipeModal" v-model="show">
            <b-row>
                <b-form>
                    <b-form-group label="Title">
                        <b-form-text v-model="this.newRecipe.title" name="title">
                        </b-form-text>
                    </b-form-group>
                    <b-form-group label="Content">
                        <b-form-textarea v-model="this.newRecipe.content" name="content">
                        </b-form-textarea>
                    </b-form-group>
                    <b-form-group label="Type">
                        <b-form-text v-model="this.newRecipe['type']" name="type">
                        </b-form-text>
                    </b-form-group>
                    <b-form-group label="Difficulty rating">
                        <b-form-rating v-model="this.newRecipe.content" name="difficultyRating" show-value
                                       show-value-max
                                       precision="1"
                                       no-border>
                        </b-form-rating>
                    </b-form-group>
                    <b-form-group label="Photo">
                        <b-form-file type="file" id="image" name="image" v-on:input="(file)=>this.uploadFile(file)">
                        </b-form-file>
                    </b-form-group>
                    <b-form-group label="Ingredients">
                        <b-list-group>
                            <b-list-group-item v-for="ingredient in this.newRecipe.ingredients" v-bind:key="ingredient">{{ingredient}}</b-list-group-item>
                        </b-list-group>
                        <b-form-text v-model="ingredient">
                        </b-form-text>
                        <b-button variant="link" v-on:click="this.addIngredient()"><font-awesome-icon :icon="['fas','plus']"></font-awesome-icon></b-button>
                    </b-form-group>
                    <b-button variant="primary" v-on:click="this.addNewRecipe()">Save</b-button>
                </b-form>
            </b-row>
        </b-modal>
    </b-container>
</template>

<script>
    export default
    {
        name: "Index",
        data()
        {
            return{
                recipes: [],
                isLoggedIn:false,
                isAdmin:false,
                newRecipe:{
                    title:"",
                    content:"",
                    ingredients: [],
                    "type":"",
                    difficultyRating: 0,
                    imageName:""
                },
                ingredient:"",
                show: false,
                filterType:"",
                filterAuthorName:"",
                filterRating:null
            };
        },
        beforeMount()
        {
            this.loadRecipes();
            let user = JSON.parse(localStorage.getItem('recipe-site.user'));
            if(user)
            {
                this.isLoggedIn=true;
                if(user.roles.includes("ADMIN"))
                {
                    this.isAdmin = true;
                }
            }
            else
            {
                this.isLoggedIn=false;
            }
        },
        methods : {
            resetFilters()
            {
                this.filterType="";
                this.filterAuthorName="";
                this.filterRating=null;
            },
            filterRecipes()
            {
                let query ="";
                if(this.filterType)
                {
                    query+="type="+this.filterType;
                }
                if(this.filterAuthorName)
                {
                    query+="authorName="+this.filterAuthorName;
                }
                if(this.filterRating !== null)
                {
                    query+="rating="+this.filterRating;
                }
                window.axios.get('api/recipe?'+query).then(res => {
                    this.recipes = res.data;
                }).catch(()=>console.log("error loading recipe data"));
            },
            loadRecipes()
            {
                window.axios.get('api/recipe').then(res => {
                    this.recipes = res.data;
                }).catch(()=>console.log("error loading recipe data"));
            },
            uploadFile(file)
            {
                let formData = new FormData();
                formData.append("image", file);
                let headers = {'Content-Type': 'multipart/form-data'};
                window.axios.post('/api/image/',formData,{headers}).then(response => {
                    this.newRecipe.imageName = response.data;
                });
            },
            addIngredient()
            {
                this.newRecipe.ingredients.push(this.ingredient);
                this.ingredient="";
            },
            showModal()
            {
                this.show=true;
            },
            addNewRecipe()
            {
                window.axios.post('/api/recipe/',this.newRecipe);
                this.loadRecipes();
                this.show=false;
            }
        },
    }
</script>

